// dprint-ignore
import launcher from '@framework/launcher.js';
// dprint-ignore
import { reloadCmds } from '@app/common/utils.js';
import { commandRouter } from '@app/router/command.js';

(async () => {
    launcher.mainRouter.use(commandRouter.routes(), commandRouter.allowedMethods());
    await launcher.run();
    reloadCmds(launcher.app);
})();
