import logger from '@app/module/logger.js';
import { httpBaseAuth } from '@framework/middleware/http-base-auth.js';
import Router from '@koa/router';
import { ParameterizedContext } from 'koa';
import bodyParser from 'koa-bodyparser';
import { env } from 'process';

import { my_exec } from '@app/common/utils.js';
import fs from 'fs';
import path from 'path';

export const commandRouter = new Router<MYState, MYContext>();

commandRouter
    .use(bodyParser());

commandRouter.get(
    '/cmd/:id',
    httpBaseAuth({
        name: env.HTTP_BASIC_AUTH_USERNAME as string,
        pass: env.HTTP_BASIC_AUTH_PASSWORD as string,
    }),
    async (ctx: ParameterizedContext<MYState, MYContext>) => {
        const cmdId = ctx.params.id;
        let cmd: string = (ctx.cmds as Record<string, string>)[cmdId];
        if (cmd == undefined) {
            ctx.status = 404;
            ctx.body = 'There is no such command\n';
            return;
        }

        if (env.NODE_ENV == 'production') {
            // cmd = reverseProcess(cmd);
        }

        return my_exec(cmd).then(ret => {
            ctx.body = ret;
            logger.info(ret, {
                file: 'exec',
                exec: { type: 'cmd', cmdId: cmdId },
                requestId: ctx.state.requestId,
                ip: ctx.ip,
            });
        }).catch(err => {
            logger.error(err.stack, {
                file: 'exec',
                exec: { type: 'cmd', cmdId: cmdId },
                requestId: ctx.state.requestId,
                ip: ctx.ip,
            });
            ctx.throw(500, err.message, { expose: env.DEBUG == 'true' });
        });
    },
);

commandRouter.get(
    '/script/:name',
    httpBaseAuth({
        name: env.HTTP_BASIC_AUTH_USERNAME as string,
        pass: env.HTTP_BASIC_AUTH_PASSWORD as string,
    }),
    async (ctx: ParameterizedContext<MYState, MYContext>) => {
        const scriptName = ctx.params.name;
        const scriptFile = path.resolve(`./script/${scriptName}`);

        // è„šæœ¬ä¸å­˜åœ¨
        if (!fs.existsSync(scriptFile)) {
            ctx.status = 404;
            ctx.body = 'There is no such script\n';
            return;
        }

        return my_exec(scriptFile).then(ret => {
            ctx.body = ret;
            logger.info(ret, {
                file: 'exec',
                exec: { type: 'script', scriptName: scriptName },
                requestId: ctx.state.requestId,
                ip: ctx.ip,
            });
        }).catch(err => {
            logger.error(err.stack, {
                file: 'exec',
                exec: { type: 'script', scriptName: scriptName },
                requestId: ctx.state.requestId,
                ip: ctx.ip,
            });
            ctx.throw(500, err.message, { expose: env.DEBUG == 'true' });
        });
    },
);
