import logger from '@app/module/logger.js';
import { exec } from 'child_process';
import { readFile } from 'fs/promises';
import Koa from 'koa';
import path from 'path';
import { env } from 'process';

export const my_exec = (cmd: string): Promise<string> => {
    return new Promise((resolve, reject) => {
        exec(cmd, (error, stdout, stderr) => {
            if (error) {
                reject(error);
                return;
            }
            if (stderr) {
                reject(stderr);
                return;
            }
            resolve(stdout);
        });
    });
};

export const reverseProcess = (swapped: string): string => {
    const restoredBase64 = swapped.replace(/(.)(.)/g, '$2$1');
    return Buffer.from(restoredBase64, 'base64').toString('utf-8');
};

const loadCmds = async () => {
    try {
        const data = await readFile(path.resolve('cmd.json'), 'utf-8');
        return JSON.parse(data);
    } catch (error) {
        logger.error('Failed to load cmd.json:', error);
        return {};
    }
};
export const reloadCmds = (app: Koa) => {
    // å®šæ—¶è‡ªåŠ¨ cmd.json
    setInterval(async () => {
        app.context.cmds = await loadCmds();
        logger.info('Reload command list');
    }, parseInt(env.LOADCMDINTERVAL as string) || 2000);
};
